/*==============================global==============================*/

html, body {
    max-width: 100%;
    overflow-x: hidden;
    background: $lightgrey;
}

body {
	padding-top: 63px;
	// padding-bottom: 50px;
	@media (max-width: 600px) {
		padding-top: 42px;
	}
}

p {
	// width: 238px;
	padding: 0 20px;
	text-align: center;
}

select {
	// font-size: 22px;
	padding-top: 4px;
}

button {
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;
	color: #555;
	font-size: 14px;
	padding: 6px 12px;
	&:active {
		box-shadow: 0 3px 5px rgba(0, 0, 0, 0.125) inset;
		border: 1px solid black;
	}
	&:focus {
		outline: thin dotted;
		outline-offset: -2px;
	}
	&:hover, &:focus, &:active {
		background-color: #e6e6e6;
		border-color: #adadad;
		color: #333;
	}
}

input {
	padding: 0;
	margin: 0;
	border: 0;
	overflow: scroll;
	background: transparent;
	text-align: center;
}

input[type="checkbox"] {
	// height: 34px;
	// width: 34px;
	margin: 0;
}



th, table {
	td {
		vertical-align: middle !important;
	}
	text-align: center;
	@media (max-width: 600px) {
		font-size: 7px;
	}
}


caption {
	padding: 8px;
	font-weight: bold;
}

li {
	list-style-type: none;
}

h1 {
	@media (max-width: 600px) {
		font-size: 20px;
		margin: 2px 2px;
	}
}

